import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ApiService } from './api.service';
import { AccountService } from './account.service';

export interface ChatMessage {
  message: string;
  username: string;
}

@Injectable({
  providedIn: 'root'
})
export class ChatService {
  constructor(
    private readonly api: ApiService,
    private readonly accountService: AccountService,
  ) { }

  getMessagesForImageId(imgId: number): Observable<ChatMessage[]> {
    return this.api.get<ChatMessage[]>(`/api/chat/${imgId}`);
  }

  post(imgId: number, message: string) {
    const sessionKey = this.accountService.sessionKey
    return this.api.post(`/api/chat/${imgId}`, {
      sessionKey,
      message,
    }).toPromise();
  }
}
