import { Component, OnInit } from '@angular/core';
import { FormAccountSubmitData } from '../form-account/form-account.component';
import { AccountService } from '../services/account.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-page-home',
  templateUrl: './page-home.component.html',
  styleUrls: ['./page-home.component.css']
})
export class PageHomeComponent implements OnInit {
  constructor(
    public readonly accountService: AccountService,
    private readonly router: Router,
  ) { }

  ngOnInit(): void {
  }

  async login(e: FormAccountSubmitData) {
    try {
      await this.accountService.login(e.username, e.password);
    } catch (error) {
      alert(`Unable to login: ${JSON.stringify(error, null, 2)}`);
    }
  }

  async createAccount(e: FormAccountSubmitData) {
    try {
      await this.accountService.createAccount(e.username, e.password);
      await this.accountService.login(e.username, e.password);
    } catch (error) {
      alert(`Unable to login: ${JSON.stringify(error, null, 2)}`);
    }
  }

}
