import { Injectable } from '@angular/core';
import { ApiService } from './api.service';
import { tap, map } from 'rxjs/operators';

interface Session {
  id: number;
  userId: number;
  sessionKey: string;
}

@Injectable({
  providedIn: 'root'
})
export class AccountService {
  sessionKey: string;

  constructor(
    private readonly api: ApiService,
  ) {
    var stored = localStorage.getItem("ICA_SessionKey");
    if (stored) {
      this.sessionKey = stored;
    }
  }

  createAccount(username: string, password: string) {
    return this.api.post(`/api/account`, { username, password }).toPromise();
  }

  logout() {
    this.sessionKey = undefined;
    localStorage.removeItem("ICA_SessionKey");
    return Promise.resolve();
  }

  login(username: string, password: string) {
    return this.api.post<Session>(`/api/account/login`,
      { username, password })
      .pipe(
        map(x => x.sessionKey),
        tap(x => {
          this.sessionKey = x;
          localStorage.setItem("ICA_SessionKey", x);
        })
      ).toPromise();
  }
}
