import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router'
import { map, switchMap, shareReplay, take } from 'rxjs/operators';
import { ImageService, Image } from '../services/image.service';
import { ChatService, ChatMessage } from '../services/chat.service';
import { Observable, combineLatest, interval } from 'rxjs';

@Component({
  selector: 'app-image-chat',
  templateUrl: './image-chat.component.html',
  styleUrls: ['./image-chat.component.css']
})
export class ImageChatComponent implements OnInit {
  imageId$: Observable<number>;
  image$: Observable<Image>;
  messages$: Observable<ChatMessage[]>;
  newMessage = "";

  constructor(
    private readonly activatedRoute: ActivatedRoute,
    private readonly imageService: ImageService,
    private readonly chatService: ChatService,
  ) { }

  ngOnInit(): void {
    this.imageId$ = this.activatedRoute.paramMap.pipe(
      shareReplay({ refCount: true, bufferSize: 1 }),
      map(x => parseInt(x.get("imageId"), 10)),
    );

    this.image$ = this.imageId$.pipe(
      switchMap(x => this.imageService.getById(x)),
    );

    this.messages$ = combineLatest([interval(1000), this.imageId$])
      .pipe(
        map(([interval, imageId]) => imageId),
        switchMap(x => this.chatService.getMessagesForImageId(x))
      );
  }

  async send() {
    this.newMessage = this.newMessage.trim();
    if (this.newMessage === "") {
      return;
    }

    console.log("Waiting");


    this.imageId$.pipe(take(1)).subscribe(async (imageId) => {
      try {
        console.log("posting");
        await this.chatService.post(imageId, this.newMessage);
        this.newMessage = ""
      } catch (error) {
        alert("Unable to post message: " + JSON.stringify(error, null, 2));
      }
    });
  }

}
